import { createSlice } from '@reduxjs/toolkit'
import { addTwo } from './CounterSliceB';

const counterSliceA = createSlice({
    countSlice:"counterSliceA",
    initialState:0,
    reudcers:{
        add(state,action){
            return state+1;
        },
        add5(state,action){
            return state+5
        }
    },
    extraReducers:{
        [addTwo](state,action){
            return state+2;
        }     
    }
})

const  {actions,reudcers}  = counterSliceA;

export const {add,add5} = actions;
export default reducer;


